@use 'base/mixins' as m;
@use 'base/variables' as v;

.noticias{

    position:relative;

    margin: 0 auto;
    text-align:center;
    max-width:90%;

    background-color: darken(v.$crema, 8%);
    padding-top: 3rem;
    



    

    .contenedor-slider{
        min-height: 50rem;
        margin: 0 auto;
            
    }
        
    

    .scrollWrapper   {
        width:80%;
        height:300px;
        overflow:hidden;
        border:2px solid v.$morado;
        font-family:v.$fuente_principal;
        font-size:0.8em;
        text-align:center;
        box-shadow: 1px 2px 3px #333;
        // background-color: darken(v.$crema, 10%);

        .scrollTitle {
            background-color:v.$morado;
            color:#fff;
            font-family:v.$fuente_principal;
            padding:5px;
            font-size: 1.1em;
            font-weight:bold;
            text-align:center;
        }
        #scroll    {
            position:relative;
            width:auto;
            margin:1px;
            z-index: 1;
            padding:5px;
            .titulo-noticia{
               h4{
                   font-size:1.5em;
                }
            }
            .noticia-cuerpo{
                p{
                    color:v.$morado;
                    font-size:1.2em;
                    a{
                        color:v.$morado;
                        font-weight: bold;
    
                    }
                }
            }
        }
    }

    // Opción 2. Carrusel automático

    * {
		box-sizing: border-box;
	}



	.noticias > ul {
		position: relative;
		top: 50%;
		width: 800px;
		height: 200px; 
		left: 50%;
		margin-left: -400px;
		margin-top: -130px;
	}
 
	ul > li {
		width: 50%;
		list-style-type: none;
		position: absolute;
		top: 30px;
		height: 200px; 
		opacity: 0;
		padding-top: 20px;
		text-align: center;
		transition: 1s opacity; 
	}

	.active {
		opacity: 1;
	}

    .enlace{
        padding: 5rem 0;
        
        
    }
  
	p {
		font-family: v.$fuente_principal;
		font-size: 13px;
        color: v.$morado;
		line-height: 1.5em;

        
	}
    

	strong { 
    color: darken(v.$morado,10%);
    font-weight: bold;
		font-size: 40px; 
    line-height: 50px; 
	}

    .imagen-noticia{
        display:none;

        @include m.telefono {
            display:inline-block;
            max-height: 300px;
            border-radius: 5px;
            box-shadow: 2px 3px 1px v.$moradoTransparente;
        }

    }
    

    

}