  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  min-width: 350px;
  display: flex;
  flex-direction: column;
  background-color: #EEEEEE;
  color: #5a0231;
}

*, *:before, *:after {
  box-sizing: inherit;
}

