  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 3;
}
@media (min-width: 768px) {
  header .title-header .box-header {
    justify-content: center;
  }
}
header .title-header .box-header.fijo {
  position: fixed;
  min-width: 320px;
  width: 100%;
  top: 0;
  left: 0;
  box-shadow: 0.1rem 0.2rem 0.3rem #333;
  background: linear-gradient(to right, rgb(68, 1, 29), rgb(146, 93, 115) 20%, rgb(146, 93, 115) 80%, rgb(68, 1, 29));
}
@media (min-width: 768px) {
  header .title-header .box-header.fijo {
    display: none;
  }
}
header .title-header h1 {
  color: #FFFFFF;
  text-align: left;
  margin: 0 2rem;
  text-shadow: 2px 2px #5a0231;
  text-transform: uppercase;
  font-size: 2rem;
}
@media (min-width: 768px) {
  header .title-header h1 {
    margin: 0 4rem;
    line-height: 4rem;
    font-size: 5.5rem;
  }
}
header .title-header .menu-bars {
  color: #FFFFFF;
  position: relative;
  background: transparent;
  min-height: 50px;
  z-index: 3;
}
@media (min-width: 768px) {
  header .title-header .menu-bars {
    display: none;
  }
}
header .title-header .menu-bars a {
  display: block;
  color: #5a0231;
  text-decoration: none;
  padding: 10px 20px;
  border-bottom: 2px, solid, #5a0231;
  border-radius: 5px;
  font-weight: bold;
  background-color: #FFFFFF;
}
header .title-header .menu-bars input[type=checkbox], header .title-header .menu-bars .fa-bars, header .title-header .menu-bars .fa-times {
  position: absolute;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  right: 10px;
  top: 0;
  width: 50px;
  height: 50px;
}
header .title-header .menu-bars .fa-bars, header .title-header .menu-bars .fa-times {
  font-size: 50px;
  pointer-events: none;
}
header .title-header .menu-bars input[type=checkbox] {
  opacity: 0;
}
header .title-header .menu-bars nav {
  display: none;
  position: absolute;
  top: 30px;
  right: 10px;
}
header .title-header .menu-bars nav li {
  list-style: none;
}
header .title-header .menu-bars input:checked ~ nav {
  display: block;
}
header .title-header .menu-bars input:not(:checked) ~ nav {
  display: none;
}
header .title-header .menu-bars input:checked ~ .fa-bars {
  display: none;
}
header .title-header .menu-bars input:not(:checked) ~ .fa-times {
  display: none;
}
header .barra.fijo {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  box-shadow: 0.1rem 0.2rem 0.3rem #333;
  z-index: 3;
}
header .menu-principal {
  display: none;
