  overflow: hidden;
  border: 2px solid #5a0231;
  font-family: "Cinzel", sans-serif;
  font-size: 0.8em;
  text-align: center;
  box-shadow: 1px 2px 3px #333;
}
.noticias .scrollWrapper .scrollTitle {
  background-color: #5a0231;
