.noticias .scrollWrapper #scroll {
  position: relative;
  width: auto;
  margin: 1px;
  z-index: 1;
  padding: 5px;
}
.noticias .scrollWrapper #scroll .titulo-noticia h4 {
  font-size: 1.5em;
}
.noticias .scrollWrapper #scroll .noticia-cuerpo p {
  color: #5a0231;
