.seccion-galerias {
  min-height: 300px;
}
@media (min-width: 1024px) {
  .seccion-galerias .galerias {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.seccion-galerias .galerias .galeria {
  align-items: center;
  background-color: #dadada;
  height: auto;
  justify-content: center;
  padding: 2rem 1rem;
}
@keyframes scroll {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-1950px);
  }
}
.seccion-galerias .galerias .slider {
  background-color: #dadada;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
  height: 217px;
  margin: auto;
  overflow: hidden;
  position: relative;
  max-width: 500px;
  width: auto;
  border-radius: 20px;
}
@media (min-width: 768px) {
  .seccion-galerias .galerias .slider {
    max-width: 600px;
  }
}
@media (min-width: 1024px) {
  .seccion-galerias .galerias .slider {
    max-width: 450px;
  }
}
.seccion-galerias .galerias .slider::before, .seccion-galerias .galerias .slider::after {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.8) 0%, rgba(255, 255, 255, 0) 40%);
  content: "";
  height: 217px;
  position: absolute;
  width: 325px;
  z-index: 2;
}
.seccion-galerias .galerias .slider::after {
  right: 0;
  top: 0;
  transform: rotateZ(180deg);
}
.seccion-galerias .galerias .slider::before {
  left: 0;
  top: 0;
}
.seccion-galerias .galerias .slider .slide-track1 {
  animation: scroll 80s linear infinite;
  display: flex;
  width: 4550px;
}
.seccion-galerias .galerias .slider .slide-track2 {
  animation: scroll 90s linear infinite;
  display: flex;
  width: 4550px;
}
.seccion-galerias .galerias .slider .slide-track3 {
  animation: scroll 100s linear infinite;
  display: flex;
  width: 4550px;
}
.seccion-galerias .galerias .slider .slide {
  height: 217px;
  width: 325px;
}

