.elenco .caifas {
  background-image: url(../img/elenco/caifas.jpg);
}
.elenco .pilatos {
  background-image: url(../img/elenco/pilatos.jpg);
}
.elenco .juan {
  background-image: url(../img/elenco/juan.jpg);
}

.direction {
  max-width: 100rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .direction {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.direction .direccion {
  min-width: 150px !important;
  min-height: 30rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  max-width: 310px;
  margin: 0 auto;
  border-radius: 5px;
}
@media (min-width: 1024px) {
  .direction .direccion {
    min-height: 450px !important;
  }
}
.direction .cordinacion {
  background-image: url(../img/elenco/cordinacion.jpg);
  background-position: center top !important;
}
.direction .arte {
  background-image: url(../img/elenco/arte.jpg);
}
.direction .tecnica {
  background-image: url(../img/elenco/tecnica.jpg);
}

.boton {
  background-color: #5a0231;
  padding: 1rem 2rem;
  color: #FFFFFF;
  margin-top: 1rem;
  font-size: 1.5rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  margin: 0 auto;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  box-shadow: 2px 2px #FFFFFF;
  border-radius: 5px;
}
.boton:hover {
  background-color: #8c034c;
  cursor: pointer;
}

.edicionesAnteriores {
  margin-top: 4rem;
}

.contenedor-ediciones {
  max-width: 1132px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-ediciones {
    display: flex;
  }
}
.contenedor-ediciones .lista-ediciones {
  margin: 0 2rem;
  min-width: 50%;
  text-align: center;
  border-left: 15px solid #410123;
  border-right: 15px solid #410123;
}
.contenedor-ediciones .lista-ediciones p {
  margin: 12rem 0;
  background-color: #410123;
}
.contenedor-ediciones .lista-ediciones .btnedicion {
  background-color: #5a0231;
  color: #FFFFFF;
  padding: 2rem 4rem;
  font-weight: bold;
  border-radius: 5px;
  border: 2px solid #820347;
}
.contenedor-ediciones .lista-ediciones .btnedicion:hover {
  background-color: #820347;
}
.contenedor-ediciones .imagen-ediciones {
  display: none;
}
@media (min-width: 768px) {
  .contenedor-ediciones .imagen-ediciones {
    display: block;
  }
}
.contenedor-ediciones .imgEdiciones {
  height: 100%;
  width: 100%;
}